#App {
  text-align: center;
}
.unsplash{
  display: flex;
  justify-content: space-evenly;
  margin: 10px 5%;
  box-shadow: 1px 1px 5px grey;
  padding: 15px;
}
.inputfield{
  width: 400px;
  height: 40px;
  border: 2px solid grey;
  border-radius: 30px;
}
button{
  width: 150px;
  height: 40px;
  border: 2px solid grey;
  border-radius: 30px;
}
label{
  font-size: 30px;
  height: 40px;
}
.showimg{
  display: flex;
  flex-wrap: wrap;
justify-content: space-around;
}
.item{
  width: 30%;
  margin: 10px;
  border: 4px solid ;
  border-radius: 30px;
}